.gridder.test {
  display: flex;
}

.gridder.test .gridder-item {
  height: 150px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  border: 1px solid #eee;
}

.gridder.test .gridder-item:hover {
  border: 1px solid #c5c5c5;
}

.gridder.test .gridder-item.active {
  background-color: #d3d4d4;
}

.gridder.test .gridder-show {
  border: 1px solid #eee;
}

.gridder.test .gridder-navigation {
  width: 100%;
  float: right;
  text-align: right;
}

.gridder.test .gridder-navigation a {
  cursor: pointer;
  border: 1px solid #eee;
  margin-left: 5px;
  padding: 5px;
  display: inline-block;
}

.gridder.test .gridder-navigation a:hover {
  background: #eee;
}

.gridder.theme_1 {
  padding: 20px;
}

.gridder.theme_1 .gridder-item {
  cursor: pointer;
  border: 1px solid #eee;
}

.gridder.theme_1 .gridder-item:hover {
  background: #f7f7f7;
  border: 1px solid #c5c5c5;
}

.gridder.theme_1 .gridder-item.active {
  background-color: #d3d4d4;
  padding: 10px;
}

.gridder.theme_1 .gridder-item img {
  width: 100%;
}

.gridder.theme_1 .gridder-show {
  border: 1px solid #eee;
  padding: 15px;
  position: relative;
}

.gridder.theme_1 .gridder-show.gridder-bottom img {
  width: 40%;
  float: left;
  margin-bottom: 20px;
  margin-right: 20px;
}

.gridder.theme_1 .gridder-show.gridder-right img {
  width: 100%;
}

.gridder.theme_1 .gridder-navigation {
  width: 50%;
  text-align: right;
  position: absolute;
  top: 20px;
  right: 20px;
}

.gridder.theme_1 .gridder-navigation a {
  cursor: pointer;
  background: #fff;
  border: 1px solid #eee;
  margin-left: 5px;
  padding: 5px;
  display: inline-block;
}

.gridder.theme_1 .gridder-navigation a:hover {
  background: #eee;
}

.gridder.theme_2 {
  background: #181818;
  padding: 20px;
}

.gridder.theme_2 .gridder-item {
  height: 250px;
  position: relative;
  overflow: hidden;
}

.gridder.theme_2 .gridder-item img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  object-position: center;
}

.gridder.theme_2 .gridder-list-details {
  width: 100%;
  top: 35%;
  left: 0x;
  color: #fff;
  text-align: center;
  position: absolute;
}

.gridder.theme_2 .gridder-show {
  color: #fff;
  padding: 20px;
  position: relative;
}

.gridder.theme_2 .gridder-navigation {
  width: 100%;
  height: 100%;
  position: absolute;
}

.gridder.theme_2 .gridder-navigation a {
  height: 40px;
  color: #000;
  cursor: pointer;
  background: #fff;
  padding: 10px 5px;
  text-decoration: none;
  position: absolute;
}

.gridder.theme_2 .gridder-navigation a.gridder-prev {
  top: 40%;
  left: 10px;
}

.gridder.theme_2 .gridder-navigation a.gridder-next {
  top: 40%;
  right: 10px;
}

.gridder.theme_2 .gridder-navigation a.gridder-close {
  top: 10px;
  right: 10px;
}

.gridder.theme_2.hasOpenExpander .gridder-item:not(.active) {
  opacity: .2;
}

