
////////////////////////////////
/* DEMO THEME */


.gridder.test{
  display:flex;
  .gridder-item{
    border: 1px solid #EEE;
    height: 150px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    &:hover{
      border: 1px solid #c5c5c5;
    }
    &.active{
      background-color: #d3d4d4;
    }
  }

  .gridder-show{
    border: 1px solid #EEE;
  }

  .gridder-navigation{
    width: 100%;
    float: right;
    text-align: right;
    a{
      display:inline-block;
      margin-left: 5px;
      padding: 5px;
      border: 1px solid #EEE;
      cursor: pointer;
      &:hover{
        background: #EEE;
      }
    }
  }
}

.gridder.theme_1{
  padding: 20px;
  .gridder-item{
    border: 1px solid #EEE;
    cursor: pointer;
    &:hover{
      border: 1px solid #c5c5c5;
      background: #F7F7F7;
    }
    &.active{
      background-color: #d3d4d4;
      padding: 10px;
    }
    img{
      width: 100%;
    }
  }

  .gridder-show{
    position: relative;
    border: 1px solid #EEE;
    padding: 15px;

    // different layouts
    &.gridder-bottom{
      img{width: 40%; margin-right: 20px; margin-bottom: 20px; float:left;}
    }

    &.gridder-right{
      img{width: 100%;}
    }
  }

  .gridder-navigation{
    right: 20px;
    top: 20px;
    position: absolute;
    width: 50%;
    text-align: right;
    a{
      display:inline-block;
      background: #FFF;
      margin-left: 5px;
      padding: 5px;
      border: 1px solid #EEE;
      cursor: pointer;
      &:hover{
        background: #EEE;
      }
    }
  }

  
}

.gridder.theme_2{
  padding: 20px;
  background: #181818;
  .gridder-item{
    overflow: hidden;
    position: relative;
    height: 250px;
  }
  .gridder-item img{
    width: 100%;
    height: 250px;

    object-fit: cover;
    object-position: center;
  }
  .gridder-list-details{
    position: absolute;
    width: 100%;
    top: 35%;
    left: 0x;
    color: #FFF;
    text-align: center;
  }
  .gridder-show{
    padding: 20px;
    color: #FFF;
    position: relative;
  }

  .gridder-navigation{
    position: absolute;
    width: 100%;
    height: 100%;
    a{
      background: #FFF;
      position: absolute;
      height: 40px;
      padding: 10px 5px; 
      text-decoration: none;
      color: #000;
      cursor: pointer;
      &.gridder-prev{
        top: 40%;
        left: 10px;
      }
      &.gridder-next{
        top: 40%;
        right: 10px;
      }
      &.gridder-close{
        top: 10px;
        right: 10px;
      }
    }
  }

  &.hasOpenExpander .gridder-item:not(.active){
    opacity: .2;
  }
}
 
 